import { Injectable } from '@angular/core';
import {
  IifBehaviorType,
  STColumnButtonDrawerConfig,
  STColumnButtonModalConfig,
  STColumnButtonPop,
  STIcon,
  STMultiSort,
  STPage,
  STReq,
  STRes,
  STRowClassName,
  STSingleSort,
  STWidthMode,
} from './table.interfaces';

@Injectable({ providedIn: 'root' })
export class STConfig {
  /**
   * `1`
   */
  pi?: number;
  /**
   *  `0` `10`
   */
  ps?: number;
  /**
   * 
   */
  bordered?: boolean;
  /**
   * table
   */
  size?: 'small' | 'middle' | 'default' = 'default';
  /**
   * `true`
   */
  responsive?: boolean = true;
  /**
   * `false`
   */
  responsiveHideHeaderFooter?: boolean = false;
  /**  */
  req?: STReq = {
    type: 'page',
    method: 'GET',
    allInBody: false,
    lazyLoad: false,
    reName: { pi: 'pi', ps: 'ps', skip: 'skip', limit: 'limit' },
  };
  /**  */
  res?: STRes = {
    reName: { list: ['list'], total: ['total'] },
  };
  /**  */
  page?: STPage = {
    front: true,
    zeroIndexed: false,
    position: 'bottom',
    placement: 'right',
    show: true,
    showSize: false,
    pageSizes: [10, 20, 30, 40, 50],
    showQuickJumper: false,
    total: true,
    toTop: true,
    toTopOffset: 100,
  };
  /**
   * `columns` 
   */
  sortReName?: { ascend?: string; descend?: string };
  /**
   * 
   * - `columnName=ascend|descend`
   * - `sort=columnName.(ascend|descend)`
   */
  singleSort?: STSingleSort | null = null;
  /**
   *  `sort` 
   */
  multiSort?: STMultiSort | null = null;
  /**
   * 
   */
  modal?: STColumnButtonModalConfig = {
    paramsName: 'record',
    size: 'lg',
    exact: true,
  };
  /**
   * 
   */
  drawer?: STColumnButtonDrawerConfig = {
    paramsName: 'record',
    size: 'md',
    footer: true,
    footerHeight: 55,
  };
  /**
   * 
   */
  pop?: STColumnButtonPop = {
    title: '',
  };
  /**
   * `200`
   */
  rowClickTime?: number = 200;
  /**
   * 
   */
  filterConfirmText?: string;
  /**
   * 
   */
  filterClearText?: string;
  /**
   * 
   */
  btnIcon?: STIcon = {
    type: '',
    theme: 'outline',
    spin: false,
  };
  /**
   * `1`
   * - `index + noIndex`
   */
  noIndex?: number = 1;
  /**
   * 
   */
  rowClassName?: STRowClassName;
  /**
   * 
   */
  expandRowByClick?: boolean = false;
  /**
   * 
   */
  expandAccordion?: boolean = false;
  /**
   *  `width` 
   */
  widthMode?: STWidthMode = {
    type: 'default',
    strictBehavior: 'truncate',
  };
  virtualItemSize? = 54;
  virtualMaxBufferPx? = 200;
  virtualMinBufferPx? = 100;

  /**
   * Conditional expression rendering behavior, can be set to `hide` (default) or `disabled`
   */
  iifBehavior?: IifBehaviorType = 'hide';
}
